.mobile-client-build-history-item {
  border: 1px solid #d1d1d1;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: -1px;
  padding: 0; }

.mobile-client-build-history-item .status-panel {
  align-items: center;
  display: flex;
  padding: 0; }

@media (min-width: 600px) {
  .build-summary {
    flex: 0 0 125px;
    flex-direction: column;
    justify-content: center; } }

.build-summary {
  display: flex;
  flex-wrap: wrap;
  padding: 5px;
  position: relative;
  text-align: center; }

.mobile-client-build-history-item .status-panel .state {
  display: flex;
  flex-wrap: wrap;
  padding: 0; }

.mobile-client-build-history-item .status-panel .state .info {
  display: inline;
  padding-right: 12px; }

.mobile-client-build-history-item .status-panel .state .info.logs {
  padding-right: 60px; }

.status {
  min-width: 130px; }

.mobile-client-build-history-item .build-timestamp {
  color: #72767b;
  font-size: 91%; }

.mobile-client-build-history-item .download {
  display: flex;
  flex-direction: column;
  justify-content: center; }

.mobile-client-build-history-item .download button {
  flex-shrink: 1;
  margin: 0 0 0 auto; }
